import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { WithSubRoute } from '../../components/WithSubRoute';

class TechnologyPage extends Component {

    render(){

        const { routes } = this.props;

        return(
            <div>
                {routes.map((route, idx) => {
                    return route.component ? (
                        <WithSubRoute key={idx} {...route} />
                    ) : (null);
                },)}
            </div>
        );

    }

}

function mapStateToProps(state) {
    const { technologies, authentication } = state;

    return {
        technologies,
        authentication
    }
}

export default withRouter(connect(mapStateToProps)(TechnologyPage))


